import React from "react";
import coding from "./coding.png";
import coding2 from "./coding2.png";

function Projects() {
  return (
    <div>
      <section id="showcase">
        <div className="container">
          <h1>Coding is poetry, Coding is fun</h1>
          <br />
          <ul></ul>
        </div>
      </section>
      <div className="container-1">
        <div className="box-1">
          <img src={coding2} alt="Coding is fun by Zack" height="166" />
        </div>
        <div className="box-2">
          <img src={coding} alt="Coding is fun by Bill" height="166" />
        </div>
      </div>
    </div>
  );
}

export default Projects;
